.site-list {}

.site-item:first-child {
    border-top: 1px solid #232323;
}
.site-item {
    border-bottom: 1px solid #232323;
    display: flex;
    padding: 10px;
    justify-content: space-between;
    text-decoration: none;
}
.site-item--arrow {
    font-weight: 800;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
}
.site-item--details {}

.filters-container {
    background: #ededed;
    width: 100%;
    display: none;
    position: absolute;
    height: 50vh;
}

.filters-container.visible {
    display: block;
}

.site-header {
    display: flex;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid grey;
}

.back-link-container {
    width: 50px;
    text-align: center;
}
.back-link-container a {
    font-weight: 800;
    font-size: 1.5rem;
    text-decoration: none;
}

.details--item {
    display: flex;
    padding: 10px 0;
}
.details--icon {
    width: 50px;
    text-align: center;
}
.details--value {}

@media (min-width: 600px) {
    .site-list {
        width: 60vw;
        margin: 0 auto;
    }
}